import React, { Component } from 'react';

import { connect } from 'react-redux';

import './Preview.css';

import Frame from '../frames/Frame';

const mapStateToProps = state => ({
  ...state
});

const mapDispatchToProps = dispatch => ({});

class Preview extends Component {
  constructor(props) {
    super(props);
    this.state = {
      counter: 0,
      countFrame: 0,
      interval: '',
    };
  }

  getNextFrame() {
    this.setState({ countFrame: this.props.frames.frames.length - 1 });
    if (this.state.counter >= this.state.countFrame) {
      this.setState({ counter: 0 });
    } else {
      this.setState({ counter: this.state.counter + 1 });
    }
  }

  handleSpeedChange(e) {
    clearInterval(this.state.interval);
    const intervalId = setInterval(this.getNextFrame.bind(this), 1000/+e.target.value);
    this.setState({
      interval: intervalId,
    });
  }

  componentDidMount() {
    const intervalId = setInterval(this.getNextFrame.bind(this), 1000);
    this.setState({
      interval: intervalId,
    });
  }

  componentWillUnmount() {
    clearInterval(this.state.interval);
  }

  render() {
    let frame = this.state.counter;

    let fields = [];

    for (let i = 0; i < this.props.frames.frames.length; i++) {
      fields.push(<Frame index={i} key={i.toString()} />);
    };

    return (
      <section className="preview">
        <div className="preview-area">
          {fields[frame]}
        </div>
        <div>
          <datalist id="tickmarks">
            <option value="1" label="1" />
            <option value="5" label="5" />
            <option value="10" label="10" />
            <option value="15" label="15" />
            <option value="20" label="20" />
            <option value="24" label="24" />
          </datalist>
          <input onChange={this.handleSpeedChange.bind(this)} type="range" id="start" name="speed" min="1" max="24" list="tickmarks" />
        </div>
        <h3>FPS</h3>
      </section>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Preview);
