import React, { Component } from 'react';

import { connect } from 'react-redux';

import { changeColor, changeTool } from '../actions/frameActions';

const mapStateToProps = state => ({
  ...state
});

const mapDispatchToProps = dispatch => ({
  changeColor: (color) => dispatch(changeColor(color)),
  changeTool: (tool) => dispatch(changeTool(tool))
});

class Tool extends Component {

  handlePaintBucketChange() {
    const color = document.getElementById('current').classList;
    this.props.changeTool('PaintBucket');
    this.props.changeColor(color.value);
  }

  handleChooseColorChange() {
    this.props.changeTool('ChooseColor');
  }

  handleColorsPartChange(event) {
    if (this.props.frames.tool === 'ChooseColor') {
      const { target } = event;
      if (target.hasAttribute('data-color')) {
        const color = target.getAttribute('data-color');
        const currentColorItem = document.getElementById('current');
        currentColorItem['data-color'] = color;
        document.getElementById('prev-color').className = currentColorItem.classList;
        document.getElementById('prev-color')['data-color'] = currentColorItem.classList;
        currentColorItem.className = color
        this.props.changeColor(
          color,
        );
      } else if (target.parentNode.hasAttribute('data-color')) {
        const color = target.parentNode.getAttribute('data-color');
        const currentColorItem = document.getElementById('current');
        currentColorItem['data-color'] = color;
        document.getElementById('prev-color').className = currentColorItem.classList;
        document.getElementById('prev-color')['data-color'] = currentColorItem.classList;
        currentColorItem.className = color
        this.props.changeColor(
          color,
        );
      }
    }
  }

  render() {
    return (
      <section className="tool">
        <div className="palette">
          <p id="paint-bucket" onClick={this.handlePaintBucketChange.bind(this)}>
            <svg width="20" height="18" viewBox="0 0 20 18" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path
                d="M17.7782 11.25C17.7782 11.25 15.556 14.5072 15.556 15.75C15.556 16.9928 16.5511 18 17.7782 18C19.0053 18 20.0004 16.9928 20.0004 15.75C20.0004 14.5072 17.7782 11.25 17.7782 11.25ZM17.4528 7.63102L10.2414 0.329414C10.0244 0.109687 9.73999 0 9.45561 0C9.17124 0 8.88686 0.109687 8.67019 0.329414L5.83755 3.19746L2.84519 0.167344C2.62818 -0.0523828 2.27644 -0.0523828 2.05978 0.167344L1.27436 0.962578C1.05769 1.1823 1.05769 1.53844 1.27436 1.75781L4.26707 4.78758L0.97679 8.11898C-0.324946 9.43664 -0.324946 11.5734 0.97679 12.8914L5.04589 17.0114C5.69658 17.6702 6.54971 18 7.40248 18C8.25561 18 9.10839 17.6706 9.75908 17.0114L17.4525 9.22184C17.8865 8.78238 17.8865 8.07047 17.4528 7.63102ZM13.4174 10.1261H2.28964C2.33686 9.99105 2.4136 9.84551 2.54762 9.71016L3.00422 9.24785L5.8379 6.37875L7.87262 8.43891C8.3063 8.87801 9.00978 8.87801 9.44346 8.43891C9.87714 7.9998 9.87714 7.28754 9.44346 6.84844L7.40874 4.78828L9.45561 2.71582L15.0959 8.4266L13.4174 10.1261Z"
                fill="black" fillOpacity="0.5413" />
            </svg>
            <span>Paint bucket</span>
          </p>
          <p id="choose-color" className="choose-color" onClick={this.handleChooseColorChange.bind(this)}>
            <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path
                d="M1.98242 13.0176C1.51367 13.4863 1.25 14.1223 1.25 14.7856V16.5625L0 18.75L1.25 20L3.4375 18.75H5.21445C5.87734 18.75 6.51328 18.4867 6.98203 18.018L11.9289 13.0719L6.92891 8.07189L1.98242 13.0176ZM18.9016 1.09845C17.4379 -0.366394 15.0625 -0.366394 13.5988 1.09845L10.5875 4.10978L10.0758 3.59806C9.70703 3.22931 9.11289 3.23439 8.75 3.59806L7.14961 5.19845C6.78359 5.56447 6.78359 6.15822 7.14961 6.52423L13.4754 12.85C13.8441 13.2188 14.4383 13.2137 14.8012 12.85L16.4016 11.25C16.7676 10.884 16.7676 10.2902 16.4016 9.92423L15.8898 9.41251L18.9012 6.40118C20.3664 4.93712 20.3664 2.5629 18.9016 1.09845Z"
                fill="black" fillOpacity="0.5413" />
            </svg>
            <span>Choose color</span>
          </p>
          <div className="line"></div>
          <p id="move">
            <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
              <g clipPath="url(#clip0)">
                <path
                  d="M13.7571 16.6311L10.6636 19.7247C10.2974 20.0909 9.70387 20.0909 9.33773 19.7247L6.24414 16.6311C5.65355 16.0406 6.07184 15.0307 6.90707 15.0308H8.90559L8.90555 11.093H4.96789V13.0916C4.96789 13.9268 3.95809 14.3451 3.36746 13.7545L0.273867 10.6609C-0.0922656 10.2948 -0.0922656 9.70117 0.273867 9.33508L3.36746 6.24148C3.95805 5.65089 4.96789 6.06917 4.96789 6.90441V8.90555H8.90555V4.96788H6.90438C6.06914 4.96788 5.65086 3.95808 6.24145 3.36745L9.33504 0.273852C9.70117 -0.092281 10.2947 -0.092281 10.6609 0.273852L13.7545 3.36745C14.345 3.95804 13.9268 4.96788 13.0915 4.96788H11.093V8.90555H15.0307V6.90703C15.0307 6.07179 16.0405 5.65351 16.6311 6.2441L19.7246 9.33769C20.0908 9.70383 20.0908 10.2974 19.7246 10.6635L16.6311 13.7571C16.0405 14.3477 15.0306 13.9294 15.0307 13.0942V11.093H11.093V15.0307H13.0942C13.9295 15.0307 14.3477 16.0405 13.7571 16.6311Z"
                  fill="black" fillOpacity="0.5413" />
              </g>
              <defs>
                <clipPath id="clip0">
                  <rect width="20" height="20" fill="white" />
                </clipPath>
              </defs>
            </svg>
            <span>Move</span>
          </p>
          <p id="transform">
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path
                d="M2 8.56249V7.93749C2 7.41972 2.41973 6.99999 2.9375 6.99999H17V5.12499C17 4.29034 18.0117 3.87335 18.6004 4.46206L21.7254 7.58706C22.0915 7.95319 22.0915 8.54679 21.7254 8.91288L18.6004 12.0379C18.0139 12.6243 17 12.2131 17 11.375V9.49999H2.9375C2.41973 9.49999 2 9.08026 2 8.56249ZM21.0625 14.5H7V12.625C7 11.7924 5.98977 11.3719 5.39957 11.9621L2.27457 15.0871C1.90848 15.4532 1.90848 16.0468 2.27457 16.4129L5.39957 19.5379C5.9866 20.1249 7 19.7124 7 18.875V17H21.0625C21.5803 17 22 16.5803 22 16.0625V15.4375C22 14.9197 21.5803 14.5 21.0625 14.5Z"
                fill="black" fillOpacity="0.5413" />
            </svg>
            <span>Transform</span>
          </p>
        </div>
        <div className="colors-part" onClick={this.handleColorsPartChange.bind(this)}>
          <ul>
            <li data-color="red">
              <div id="current" className="red"></div>
              <p>Current color</p>
            </li>
            <li data-color="green">
              <div id="prev-color" className="green"></div>
              <p>Prev color</p>
            </li>
            <div className="line"></div>
            <li data-color="red">
              <div className="red"></div>
              <p>red</p>
            </li>
            <li data-color="blue">
              <div className="blue"></div>
              <p>blue</p>
            </li>
            <li data-color="green">
              <div className="green"></div>
              <p>green</p>
            </li>
            <li data-color="grey">
              <div className="grey"></div>
              <p>grey</p>
            </li>
          </ul>
        </div>
      </section>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Tool);
