export const changeNumber = (number) => dispatch => {
    dispatch({
        type: 'CHANGE_NUMBER',
        number
    })
}

export const changeColor = (color) => dispatch => {
    dispatch({
        type: 'CHANGE_COLOR',
        color
    })
}

export const addFrame = () => dispatch => {
    dispatch({
        type: 'ADD_FRAME',
    })
}

export const changeColorFrame = (numberFrame, color, number) => dispatch => {
    dispatch({
        type: 'CHANGE_COLORFRAME',
        numberFrame,
        color,
        number
    })
}

export const changeTool = (tool) => dispatch => {
    dispatch({
        type: 'CHANGE_TOOL',
        tool
    })
}

export const deleteFrame = (numberDelete) => dispatch => {
    dispatch({
        type: 'DELETE_FRAME',
        numberDelete
    })
}

export const copyFrame = (numberFrame) => dispatch => {
    dispatch({
        type: 'COPY_FRAME',
        numberFrame
    })
}
