import React, { Component } from 'react';

import { connect } from 'react-redux';

import './Frame.css';

import { changeNumber } from '../../actions/frameActions';

const mapStateToProps = state => ({
  ...state
});

const mapDispatchToProps = dispatch => ({
  changeNumber: (number) => dispatch(changeNumber(number))
});

class Frame extends Component {

  handleFrameChange() {
    this.props.changeNumber(this.props.index);
  }

  render() {
    const numberFrame = this.props.index;
    const frameState = this.props.frames.frames[numberFrame];
    return (
      <section onClick={this.handleFrameChange.bind(this)} className="frame">
        <div className="frame-area">
          <div>
            <div className={frameState[0]} data-number="0"></div>
            <div className={frameState[1]} data-number="1"></div>
            <div className={frameState[2]} data-number="2"></div>
          </div>
          <div>
            <div className={frameState[3]} data-number="3"></div>
            <div className={frameState[4]} data-number="4"></div>
            <div className={frameState[5]} data-number="5"></div>
          </div>
          <div>
            <div className={frameState[6]} data-number="6"></div>
            <div className={frameState[7]} data-number="7"></div>
            <div className={frameState[8]} data-number="8"></div>
          </div>
        </div>
      </section>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Frame);
