const initialState = {
    number: 0,
    color: 'grey-light',
    tool: '',
    frames: [
        [
            'grey-light',
            'grey-light',
            'grey-light',
            'grey-light',
            'grey-light',
            'grey-light',
            'grey-light',
            'grey-light',
            'grey-light',
        ]
    ]
}

export default (state = initialState, action) => {
    switch (action.type) {
        case 'CHANGE_NUMBER':
            return {
                ...state,
                number: action.number
            }

        case 'CHANGE_COLOR':
            return {
                ...state,
                color: action.color
            }

        case 'ADD_FRAME':
            const frame = [...state.frames];
            frame.push([
                'grey-light',
                'grey-light',
                'grey-light',
                'grey-light',
                'grey-light',
                'grey-light',
                'grey-light',
                'grey-light',
                'grey-light',
            ]);
            return {
                ...state,
                frames: frame
            }

        case 'CHANGE_COLORFRAME':
            const frames1 = state.frames.map((item, indexState) => {
                if (indexState === action.numberFrame) {
                    item = item.map((elem, index) => {
                        if (index === action.number) {
                            elem = action.color;
                        }
                        return elem;
                    });
                }
                return item;
            });
            return {
                ...state,
                frames: frames1
            }

        case 'DELETE_FRAME':
            const newFrames = state.frames.filter((item, indexState) =>
                indexState !== action.numberDelete
            );
            return {
                ...state,
                frames: newFrames
            }

        case 'CHANGE_TOOL':
            return {
                ...state,
                tool: action.tool
            }

        case 'COPY_FRAME':
            const frameState = [...state.frames];
            const copyFrame = frameState[action.numberFrame];
            frameState.push(copyFrame);
            return {
                ...state,
                frames: frameState
            }

        default:
            return state
    }
}
