import React, { Component } from 'react';

import { connect } from 'react-redux';

import './Frames.css';

import Frame from './Frame';

import { addFrame, deleteFrame, changeNumber, copyFrame } from '../../actions/frameActions';

const mapStateToProps = state => ({
  ...state
});

const mapDispatchToProps = dispatch => ({
  addFrame: () => dispatch(addFrame()),
  deleteFrame: (numberDelete) => dispatch(deleteFrame(numberDelete)),
  changeNumber: (number) => dispatch(changeNumber(number)),
  copyFrame: (numberFrame) => dispatch(copyFrame(numberFrame))
});

class Frames extends Component {
  constructor(props) {
    super(props);

    this.state = {
      fieldsCount: 1
    };
  }

  addFrame() {
    this.setState({ fieldsCount: this.state.fieldsCount + 1 });
    this.props.addFrame();
  }

  deleteFrame(event) {
    if (this.state.fieldsCount > 1) {
      const { target } = event;
      this.props.deleteFrame(+target.parentNode.getAttribute('data-index'));
      this.props.changeNumber(0);
      this.setState({ fieldsCount: this.state.fieldsCount - 1 });
    }
  }

  duplicateFrame(event) {
    const { target } = event;
    this.setState({ fieldsCount: this.state.fieldsCount + 1 });
    this.props.copyFrame(+target.parentNode.getAttribute('data-index'));
  }

  render() {
    let fields = [];

    for (let i = 0; i < this.state.fieldsCount; i++) {
      fields.push(<div key={i.toString() + 5} className="frame_border">
        <div key={i.toString() + 7} className="frame_number">{i}</div>
        <div data-index={i} key={i.toString() + 8} className="frame_delete" onClick={this.deleteFrame.bind(this)}><i className="far fa-trash-alt"></i></div>
        <Frame index={i} key={i.toString()} />
        <div data-index={i} key={i.toString() + 6} className="frame_duplicate" onClick={this.duplicateFrame.bind(this)}><i className="far fa-copy"></i></div>
      </div>);
    };

    return (
      <section className="frames">
        <div>
          {fields}
        </div>
        <div>
          <button className="frames_button" onClick={this.addFrame.bind(this)} type="button"><i className="fas fa-plus"></i><p>Add new frame</p></button>
        </div>
      </section>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Frames)
