import React, { Component } from 'react';

import { connect } from 'react-redux';

import Frame from '../components/frames/Frame';

import { changeColorFrame } from '../actions/frameActions';

const mapStateToProps = state => ({
  ...state
});

const mapDispatchToProps = dispatch => ({
  changeColorFrame: (numberFrame, color, number) => dispatch(changeColorFrame(numberFrame, color, number))
});

class CanvasPlace extends Component {

  handleFramesChange(event) {
    if (this.props.frames.tool === 'PaintBucket') {
      const { target } = event;
      const color = this.props.frames.color;
      if (target.hasAttribute('data-number') || target.hasAttribute('data-color')) {
        this.props.changeColorFrame(
          this.props.frames.number,
          color,
          +target.getAttribute('data-number'),
        );
      }
    }
  }

  render() {
    return (
      <section onClick={this.handleFramesChange.bind(this)} className="canvas_place">
        <Frame index={this.props.frames.number} key={this.props.frames.number} />
      </section>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(CanvasPlace);
